<template>
  <div>
    <div class="call-to-action">
      <v-row v-for="log in logs" :key="log.id">
        <v-col class="text-left">
          <div style="background-color: #184C72; color: white; font-weight: bold">
            Estado: {{ log.status.name }}
          </div>
          <div>Fecha: {{ date(log.created_at).format('DD/MM/YYYY') }}</div>
          <div>Hora: {{ date(log.created_at).format('hh:mm A') }}</div>
          <div>Comentario: {{ log.comment }}</div>
        </v-col>
      </v-row>
    </div>
  </div>
</template>

<script>
import moment from "moment";

export default {
  props: {
    logs: Array
  },
  data: () => ({
    isLoading: true,
    logs: []
  }),
  mounted() {

  },
  methods: {
    date(date) {
      return moment(date);
    },
  }
}
</script>
