<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="12">
          <v-img
              :src="require('../../assets/logo-white.png')"
              contain
              height="80"
          />
        </v-col>

        <v-col cols="12" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title text-center">
            Estado de tus consultas
          </h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12"></v-col>
      </v-row>

      <v-row no-gutters>
        <v-col>
          <invoice :isLoading="isLoading" :item="consultation"></invoice>
        </v-col>
      </v-row>

      <logs :isLoading="isLoading" :logs="logs"></logs>

      <button-back @click="goToIndex"></button-back>
    </v-container>
  </div>
</template>

<script>

import axios from "axios"
import Invoice from '../../components/Invoice';
import Logs from '../../components/StatusLog';
import ButtonBack from "../../components/ButtonBack";

export default {
  name: 'RequestInvoice',
  components: {Invoice, Logs, ButtonBack},
  data: () => ({
    isLoading: false,
    consultation: {},
    logs: [],
  }),
  mounted() {
    this.loadConsultation()
  },
  methods: {
    loadConsultation() {
      let consultationId = this.$route.params.id
      let url = 'consultations/' + consultationId
      axios.get(url).then((response) => {
        this.consultation = response.data;
        this.loadLogs();
      }).catch(() => {
      }).then(() => {

      })
    },
    loadLogs() {
      axios.get('/logs?model_id=' + this.consultation.id + '&model_type=App\\Consultation').then((response) => {
        this.logs = response.data;
      }).catch(() => {
      }).then(() => {
        this.isLoading = false;
      });
    },
    goToIndex() {
      this.$router.push({name: 'RequestIndex'})
    }
  }
}
</script>